<template>
    <div>
       <div class="crumbs">
          <slot name="home"></slot>
          <div class="arrow"></div>
          <slot name="current"></slot>
       </div>
    </div>
 </template>
 
 <script>
 export default {
    name: "Crumbs"
 }
 </script>
 
 <style scoped>
 .crumbs {
    display: flex;
    margin-top: 24px;
    margin-left: 15px;
 }
 
 .arrow {
    width: 16px;
    height: 16px;
    background-image: url('../assets/img/icon.png');
    background-repeat: no-repeat;
    background-size: 16px;
    margin: 7px;
 }
 </style>


