import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    }, {
      path: '/',
      name: 'PageView',
      component: resolve => require(['@/view/PageView'], resolve),
      children: [
        {
          path: '/',
          redirect: '/home_en'
        }, 
        {
          path: '/home_en',
          name: 'HomePageEn',
          component: resolve => require(['@/view/HomePage_en.vue'], resolve),
          meta: {
            title: '英文首页'
          }
        },{
          path: '/accountNumberShow_en',
          name: 'accountNumberShowEn',
          component: resolve => require(['@/view/accountNumberShow_en.vue'], resolve),
          meta: {
            title: '英文视频详情'
          }
        },{
          path: '/companyintroduction_en',
          name: 'companyintroductionEn',
          component: resolve => require(['@/view/companyintroduction_en.vue'], resolve),
          meta: {
            title: '英文关于我们'
          }
        },
        {
          path: '/NewsInformation_en',
          name: 'NewsInformationEn',
          component: resolve => require(['@/view/NewsInformation_en.vue'], resolve),
          meta: {
            title: '英文公司新闻'
          }
        },
        {
          path: '/Service_en',
          name: 'ServiceEn',
          component: resolve => require(['@/view/Service_en.vue'], resolve),
          meta: {
            title: '英文相关业务'
          }
        },
        {
          path: '/home',
          name: 'home',
          component: resolve => require(['@/view/HomePage'], resolve),
          meta: {
            title: '首页'
          }
        },{
          path: '/service',
          name: 'service',
          component: resolve => require(['@/view/Service'], resolve),
          meta: {
            title: '相关服务'
          }
        }, {
          path: '/newsinformation',
          name: 'newsinformation',
          component: resolve => require(['@/view/NewsInformation'], resolve),
          meta: {
            title: '新闻动态'
          }
        }, {
          path: '/companyintroduction',
          name: 'companyintroduction',
          component: resolve => require(['@/view/CompanyIntroduction'], resolve),
          meta: {
            title: '公司介绍'
          }
        },
        {
          path: '/accountNumberShow',
          name: 'accountNumberShow',
          component: resolve => require(['@/view/accountNumberShow'], resolve),
          meta: {
            title: '视频详情'
          }
        }
      ]
    }
  ]
})



