<template>
  <div id="app">
    <router-view />
    <Footer></Footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      screenWidth: document.body.clientWidth,
    }
  },
  created() {
  },
  mounted() {
    window.onresize = () => (() => {
      this.screenWidth = document.body.clientWidth;

    })();
    console.log(this.screenWidth);
    if (this.screenWidth < 1300) {

    } else {
      // 其余尺寸
      window.location.href = 'https://hkfytz.com'
    }
  },
}
</script>
